/*푸터*/
.footer{
    background-color: #d8d7d7;
    width: 100%;
    height: 130px;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 30px;
    font-family: 'Nanum Gothic', sans-serif;
    padding-top: 20px;
    padding-bottom: 20px;
}
.footer1{
    text-align: center;
    font-size: 15px;
    color: #555;
}

.footer2{
    text-align: center;
    color: #555;
}
.footer ul li{
    display: inline-block;
}
.footer3{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
}
.footer3 a{
    margin-right:40px;
}

.footer3 ul li img{
    width: 30px;
    height: 30px;
    margin-right: 10px;
    float: left;
}
.footer a{
    text-decoration-line: none;
    color: #555;
}
.footer_content{
    margin: 10px;
}